"use client"

import { useState } from "react"
import { api } from "@/lib/api-client"

export interface GeneratedEmail {
  subject: string
  body: string
  confidence: number
  successRate: string
  model: string
}

export function useEmailGenerator() {
  const [isGenerating, setIsGenerating] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const generateEmail = async (context: string, targetRole: string): Promise<GeneratedEmail | null> => {
    setIsGenerating(true)
    setError(null)

    try {
      const response = await api.generateEmail({ context, targetRole })

      if (response.error) {
        setError(response.error)
        return null
      }

      return response.data as GeneratedEmail
    } catch (err) {
      const message = err instanceof Error ? err.message : "Failed to generate email"
      setError(message)
      return null
    } finally {
      setIsGenerating(false)
    }
  }

  return {
    generateEmail,
    isGenerating,
    error,
  }
}
