"use client"

import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Card } from "@/components/ui/card"
import { Mail, Check, Eye, Hand, X } from "lucide-react"
import { Button } from "@/components/ui/button"

interface TimelineEvent {
  id: string
  type: "sent" | "delivered" | "opened" | "clicked" | "failed"
  timestamp: string
  details: string
}

interface TimelineModalProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  campaign?: {
    id: string
    name: string
    recipient: string
    subject: string
    body: string
    events: TimelineEvent[]
  }
}

const eventIcons = {
  sent: Mail,
  delivered: Check,
  opened: Eye,
  clicked: Hand,
  failed: X,
}

const eventColors = {
  sent: "bg-blue-500/10 text-blue-600 border-blue-200",
  delivered: "bg-green-500/10 text-green-600 border-green-200",
  opened: "bg-amber-500/10 text-amber-600 border-amber-200",
  clicked: "bg-red-500/10 text-red-600 border-red-200",
  failed: "bg-gray-500/10 text-gray-600 border-gray-200",
}

export function TimelineModal({ open, onOpenChange, campaign }: TimelineModalProps) {
  if (!campaign) return null

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Email Details</DialogTitle>
        </DialogHeader>

        {/* Email Header */}
        <Card className="bg-secondary border border-border p-6 space-y-3">
          <div className="flex items-start justify-between">
            <div>
              <p className="text-sm text-muted-foreground">To:</p>
              <p className="font-medium text-foreground">{campaign.recipient}</p>
            </div>
            <Button variant="ghost" size="icon" onClick={() => onOpenChange(false)}>
              <X className="w-4 h-4" />
            </Button>
          </div>
          <div>
            <p className="text-sm text-muted-foreground">Subject:</p>
            <p className="font-medium text-foreground">{campaign.subject}</p>
          </div>
        </Card>

        {/* Timeline */}
        <div className="space-y-6">
          <div>
            <h3 className="text-sm font-semibold text-foreground mb-4">Status Timeline</h3>
            <div className="space-y-4">
              {campaign.events.map((event, idx) => {
                const Icon = eventIcons[event.type]
                const colors = eventColors[event.type]
                return (
                  <div key={event.id} className="flex gap-4">
                    <div className="flex flex-col items-center">
                      <div className={`p-3 rounded-full border-2 ${colors}`}>
                        <Icon className="w-5 h-5" />
                      </div>
                      {idx < campaign.events.length - 1 && <div className="w-1 h-8 bg-border mt-2" />}
                    </div>
                    <div className="flex-1 pt-1">
                      <p className="font-medium text-foreground capitalize">{event.type}</p>
                      <p className="text-xs text-muted-foreground mt-1">{event.timestamp}</p>
                      <p className="text-sm text-foreground mt-2">{event.details}</p>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>

          {/* Email Content */}
          <div>
            <h3 className="text-sm font-semibold text-foreground mb-4">Email Content</h3>
            <Card className="bg-secondary border border-border p-6">
              <div className="space-y-3 text-sm text-foreground font-mono whitespace-pre-wrap">{campaign.body}</div>
            </Card>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
