"use client"

import { useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { DashboardView } from "@/components/dashboard-view"
import { CampaignView } from "@/components/campaign-view"
import { EmailGeneratorView } from "@/components/email-generator-view"
import { ObservabilityView } from "@/components/observability-view"
import { SWRConfig } from "swr"

type ActiveView = "dashboard" | "campaigns" | "generator" | "observability"

export default function Home() {
  const [activeView, setActiveView] = useState<ActiveView>("dashboard")
  const [sidebarOpen, setSidebarOpen] = useState(true)

  return (
    <SWRConfig
      value={{
        shouldRetryOnError: true,
        errorRetryCount: 3,
        dedupingInterval: 60000,
      }}
    >
      <div className="flex h-screen bg-background overflow-hidden">
        <Sidebar
          activeView={activeView}
          onViewChange={setActiveView}
          open={sidebarOpen}
          onOpenChange={setSidebarOpen}
        />

        <main className="flex-1 overflow-auto">
          {activeView === "dashboard" && <DashboardView />}
          {activeView === "campaigns" && <CampaignView />}
          {activeView === "generator" && <EmailGeneratorView />}
          {activeView === "observability" && <ObservabilityView />}
        </main>
      </div>
    </SWRConfig>
  )
}
